<template>
<div class="changepsd">
  <div class="mainpage_header">
    <div class="header_border">
      <span class="header_logo">
        <img src="@/assets/images/jylogo.png"/>
      </span>
      <div class="header_title">
        <!-- <img src="@/assets/images/logo01.png"/> -->
        欢迎使用今易档案管理系统
      </div>
      <div class="header_icon">
        <span style="cursor: pointer; margin: 0 20px; visibility: hidden;">
          <el-input
            placeholder="请输入内容">
            <i slot="suffix" class="iconfont icon-sousuo1 message"></i>
          </el-input>
        </span>
        <!-- <span style="margin-left: 0px; cursor: pointer;"><i class="iconfont icon-yonghu person"></i></span> -->
        <!-- <span style="margin: 0 20px; cursor: pointer;" title="权限"><i class="iconfont icon-quanxian permssion" @click.stop="getPermission(item)"></i></span> -->
        <el-dropdown @command="handleCommand">
          <span class="el-dropdown-link" style="margin: 0 20px; cursor: pointer;" title="个人中心"><i class="iconfont icon-yonghu person"></i></span>
          <el-dropdown-menu slot="dropdown">
            <!-- <el-dropdown-item command="a">修改密码</el-dropdown-item> -->
            <el-dropdown-item divided command="b">退出登录</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
        <span style="margin: 0 20px; cursor: pointer;" class="exitIcon" title="返回" @click.stop="backToHome"><i class="iconfont icon-dingbudaohang-zhangh exit"></i></span>
      </div>
    </div>
  </div>
  <el-row :gutter="20" style="margin: 0px!important; height:90%;">
    <el-col :span="6" :offset="9" style="position: absolute; top: 15%;">
      <div>
        <h6 class="h6_text">
          <i class="iconfont icon-guanlinggenggaimima" style="font-size: 2em; color:#409EFF;"></i>
          <span style="display: inline-block; position: absolute; top: 10px;">修改密码</span>
        </h6>
        <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px" class="demo-changeruleForm" style="margin: 50px 10px 0 10px;">
          <el-form-item label="密码：" prop="pass" class="changepsd_content">
            <el-input type="password" v-model="ruleForm.pass" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="确认密码：" prop="checkPass" class="changepsd_content">
            <el-input type="password" v-model="ruleForm.checkPass" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item class="changepsd_btn">
            <el-button type="primary" class="changepsd_submitbtn" @click="submitForm('ruleForm')">保存</el-button>
            <!-- <el-button @click="resetForm('ruleForm')">重置</el-button> -->
          </el-form-item>
        </el-form>
      </div>
    </el-col>
  </el-row>
  <!--底部-->
  <div class="mainpage_footer">
    <!-- <p><a href="#">相关链接</a>&nbsp;&nbsp;&nbsp;&nbsp;<a href="#">联系我们</a>&nbsp;&nbsp;&nbsp;&nbsp;<a href="#">法律声明</a></p> -->
    <p>Copyright&nbsp;(c)&nbsp;2000-2018&nbsp;&nbsp;天津市天深科技股份有限公司</p>
  </div>
</div>
</template>
<script>
export default {
  data () {
    var validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码'))
      } else {
        if (this.ruleForm.checkPass !== '') {
          this.$refs.ruleForm.validateField('checkPass')
        }
        callback()
      }
    }
    var validatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请再次输入密码'))
      } else if (value !== this.ruleForm.pass) {
        callback(new Error('两次输入密码不一致!'))
      } else {
        callback()
      }
    }
    return {
      msg: 'ccc',
      ruleForm: {
        pass: '',
        checkPass: ''
      },
      rules: {
        pass: [
          { validator: validatePass, trigger: 'blur' }
        ],
        checkPass: [
          { validator: validatePass2, trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    backToHome () {
      this.$router.replace(
        {
          name: 'archivelist'
        }
      )
    },
    submitForm (formName) {
      console.log('ss', this.ruleForm.pass)
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.axios.get(this.api + this.easyStarAPI.ChangeUserPw, {
            params: {
              newPw: this.ruleForm.pass
            }
          }).then((result) => {
            if (result.data.code === 0) {
              this.$message({
                type: 'success',
                message: '密码修改成功!',
                duration: 2000
              })
              setTimeout(() => {
                this.exitLogin()
              }, 2000)
            }
          })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    handleCommand (command) {
      switch (command) {
        case 'b':
          this.logOut()
          break
      }
    },
    logOut: function () {
      this.$confirm('您确定退出登录吗?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        // console.log('1111111')
        let that = this
        that.$axios.post(that.api + '/Logout' + that.actionExt).then(function (res) {
          // console.log('跳入验证', res)
          if (res.data.code === 0) {
            that.$router.replace('/login')
          } else {
            that.$message.error(res.data.errMsg)
          }
        }).catch(function (error) {
          console.log(error)
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消'
        })
      })
    },
    exitLogin () {
      let that = this
      that.$axios.post(that.api + '/Logout' + that.actionExt).then(function (res) {
        console.log('跳入验证', res)
        if (res.data.code === 0) {
          console.log('dddd')
          that.$router.replace('/login')
        } else {
          that.$message.error(res.data.errMsg)
        }
      }).catch(function (error) {
        console.log(error)
      })
    },
    getPermission: function (item) {
      this.$router.push({
        name: 'permission'
      })
    }
  }
}
</script>
<style>
.changepsd{
  height: 100%;
  width: 100%;
  background-image: url("/static/assets/images/baseImg.jpg");
  background-size: cover;
 }
.mainpage_header {
  background-image: url("/static/assets/images/top-pic.jpg");
  background-size: 100% 100%;
  height: 60px;
}
.header_border {
  width: 100%;
  height: 100%;
  position: relative;
}
.header_logo {
  display: block;
  position: absolute;
  height: 100%;
  left: 8px;
  /*top: 24%; */
}
.header_logo img {
  width: auto;
  height: inherit;
}
/* .header_title {
  position: absolute;
  left: 44% !important;
  top: 36% !important;
} */
.header_title {
  height: 100%;
  line-height: 60px;
  color: #fff;
  margin-left: 9%;
  font-size: 12px;
  width: 202px;
  text-align: center;
}
.header_icon {
  color: #fff;
  position: absolute;
  right: 122px;
  top: 0;
  height: 60px;
  line-height: 60px;
}
.header_icon span {
  display: inline-block;
}
.message {
  color: #fff !important;
  font-size: 25px !important;
}
.person {
  color: #fff !important;
  font-size: 25px !important;
}
.exit {
  color: #fff !important;
  font-size: 25px !important;
}
.permssion{
  padding: 7px;
  color: #fff !important;
  font-size: 26px !important;
}
/*内容*/
.h6_text {
  font-size: 1.5em;
  text-align: center;
  font-weight: normal;
  position: relative;
  color: #999;
}
.demo-changeruleForm .changepsd_content {
  margin-bottom: 38px!important;
}
.demo-changeruleForm .changepsd_btn {
  text-align: center!important;
}
/* .demo-changeruleForm .changepsd_btn .el-form-item__content {
  margin-left: 0px!important;
} */
.demo-changeruleForm .changepsd_submitbtn {
  border-radius: 0px!important;
  padding: 12px 64px!important;
  margin-top: 50px;
}
/*底部*/
.mainpage_footer{
  text-align: center;
  color: rgb(189, 189, 189);
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
}
.mainpage_footer p {
  font-size:12px;
  font-family: '黑体';
}
.mainpage_footer p a {
  text-decoration: none;
  color: rgb(189, 189, 189);
}
</style>



